'use client';

import { useState, useEffect } from 'react';
import { Account } from '@/types';
import { getAccounts, saveAccount } from '@/lib/storage';
import { initializeSampleData } from '@/data/sampleData';
import { AccountList, AccountDetail, DashboardStats } from '@/components';
import { LayoutDashboard, List, Download, Upload } from 'lucide-react';
import { exportAllData, importData } from '@/lib/storage';

type View = 'dashboard' | 'accounts';

export default function Home() {
  const [accounts, setAccounts] = useState<Account[]>([]);
  const [selectedAccount, setSelectedAccount] = useState<Account | null>(null);
  const [view, setView] = useState<View>('dashboard');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Initialize sample data if needed
    initializeSampleData();
    // Load accounts
    const loadedAccounts = getAccounts();
    setAccounts(loadedAccounts);
    setIsLoading(false);
  }, []);

  const handleSelectAccount = (account: Account) => {
    setSelectedAccount(account);
  };

  const handleCloseDetail = () => {
    setSelectedAccount(null);
  };

  const handleUpdateAccount = (updatedAccount: Account) => {
    saveAccount(updatedAccount);
    setAccounts((prev) =>
      prev.map((a) => (a.id === updatedAccount.id ? updatedAccount : a))
    );
    setSelectedAccount(updatedAccount);
  };

  const handleExport = () => {
    const data = exportAllData();
    const blob = new Blob([data], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `account-dashboard-backup-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleImport = () => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = '.json';
    input.onchange = async (e) => {
      const file = (e.target as HTMLInputElement).files?.[0];
      if (!file) return;

      const text = await file.text();
      if (importData(text)) {
        const loadedAccounts = getAccounts();
        setAccounts(loadedAccounts);
        setSelectedAccount(null);
        alert('Data imported successfully!');
      } else {
        alert('Failed to import data. Please check the file format.');
      }
    };
    input.click();
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-[var(--muted)]">Loading...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col">
      {/* Header */}
      <header className="bg-white border-b border-[var(--border)] px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <h1 className="text-xl font-bold text-[var(--foreground)]">
              Account Dashboard
            </h1>
            <span className="text-sm text-[var(--muted)]">Clipboard Health</span>
          </div>

          <div className="flex items-center gap-4">
            {/* View Toggle */}
            <div className="flex items-center bg-[var(--secondary)] rounded-lg p-1">
              <button
                onClick={() => setView('dashboard')}
                className={`flex items-center gap-2 px-3 py-1.5 rounded-md text-sm font-medium transition-colors ${
                  view === 'dashboard'
                    ? 'bg-white text-[var(--foreground)] shadow-sm'
                    : 'text-[var(--muted)] hover:text-[var(--foreground)]'
                }`}
              >
                <LayoutDashboard className="w-4 h-4" />
                Dashboard
              </button>
              <button
                onClick={() => setView('accounts')}
                className={`flex items-center gap-2 px-3 py-1.5 rounded-md text-sm font-medium transition-colors ${
                  view === 'accounts'
                    ? 'bg-white text-[var(--foreground)] shadow-sm'
                    : 'text-[var(--muted)] hover:text-[var(--foreground)]'
                }`}
              >
                <List className="w-4 h-4" />
                Accounts
              </button>
            </div>

            {/* Data Actions */}
            <div className="flex items-center gap-2">
              <button
                onClick={handleExport}
                className="flex items-center gap-1 px-3 py-1.5 text-sm text-[var(--muted)] hover:text-[var(--foreground)] hover:bg-[var(--secondary)] rounded-lg transition-colors"
                title="Export data"
              >
                <Download className="w-4 h-4" />
              </button>
              <button
                onClick={handleImport}
                className="flex items-center gap-1 px-3 py-1.5 text-sm text-[var(--muted)] hover:text-[var(--foreground)] hover:bg-[var(--secondary)] rounded-lg transition-colors"
                title="Import data"
              >
                <Upload className="w-4 h-4" />
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="flex-1 flex overflow-hidden">
        {view === 'dashboard' ? (
          <div className="flex-1 p-6 overflow-auto">
            <DashboardStats accounts={accounts} />

            <div className="mt-6">
              <h2 className="text-lg font-semibold mb-4">All Accounts</h2>
              <div className="bg-white border border-[var(--border)] rounded-lg shadow-sm overflow-hidden">
                <div className="h-[calc(100vh-320px)]">
                  <AccountList
                    accounts={accounts}
                    onSelectAccount={handleSelectAccount}
                    selectedAccountId={selectedAccount?.id}
                  />
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="flex-1 flex overflow-hidden">
            {/* Account List */}
            <div
              className={`bg-white border-r border-[var(--border)] overflow-hidden transition-all ${
                selectedAccount ? 'w-1/2' : 'w-full'
              }`}
            >
              <AccountList
                accounts={accounts}
                onSelectAccount={handleSelectAccount}
                selectedAccountId={selectedAccount?.id}
              />
            </div>

            {/* Account Detail Panel */}
            {selectedAccount && (
              <div className="w-1/2 overflow-hidden">
                <AccountDetail
                  account={selectedAccount}
                  onClose={handleCloseDetail}
                  onUpdate={handleUpdateAccount}
                />
              </div>
            )}
          </div>
        )}
      </main>
    </div>
  );
}
